





























































































































































































































































































    export default {
        data() {
            return {
                uid: 0,
                list: [
					{
						goods_id: 0,
						goods_name: "",
						cate_id:"",
						wIndex: -1,
						goods_quantity:'',
						total_price:'',
						price:'',
						unit_name:''
					}
				],
				workmanship_data: [
					{
						workmanship_id: 0,
						project_name: "",
						cate_id:"",
						gIndex: -1,
						quantity:'',
						workmanship_price:'',
						workmanship_sub_total:''
					}
				],
                date: "",
                tel: "",
                clientId: 0,
                clientName: "",
                showClient: false,
                clientList: [],
                textClient: "",
                showGood: false,
				gongyiGood:false,
                goodList: [],
                textGood: "",
                itemIndex: 0,
				itemgIndex:0,
                preventEvent: false,
				kehu_num : 1,
				remark:'',
				time:'',
				term_date:'',
				warehouse: [],
				warehouse_gongyi:[],
				gongyiList:[],
				name:'',//客户姓名
				address:'',//客户地址
				mobile_one:'',//联系方式1
				mobile_two:'',//联系方式2
				
				discount:'',//折扣
				remarks:'',//备注
				
				total_money:'',//商品总价
				total_count:'',//商品总数
				workmanship_total_money:'',//工艺总价
				workmanship_total_count:'',//工艺总数
				order_total_money:'',//总价
            }
        },
        mounted() {
        },
        onLoad(option) {
            this.uid = this.common.get("uid")
			// 进入页面
			// this.http({
			//     url: this.api.apply_purchase_get_no,
			//     method: "POST",
			//     data: {
			//         uid: this.uid
			//     },
			//     success: res => {
			// 		this.no = res.no
			//     }
			// })
			// 销售
			this.http({
			    url: this.api.get_user_list,
			    method: "POST",
			    data: {
			        uid: this.uid,
			    },
			    success: res => {
			        this.clientList = res
					console.log('this.clientListxxxx',this.clientList)
			    }
			})
			// 商品分类接口
			this.http({
			    url: this.api.get_goods_cate,
			    method: "POST",
			    data: {
			        uid: this.uid
			    },
			    success: res => {
			        this.warehouse = res
			    }
			})
			// 工艺分类接口
			this.http({
			    url: this.api.get_workmanship_cate,
			    method: "POST",
			    data: {
			        uid: this.uid
			    },
			    success: res => {
			        this.warehouse_gongyi = res
			    }
			})
			setTimeout(() =>{
				this.clientId = this.uid
				this.clientList.map((item,index) =>{
					if(item.id == this.clientId) {
						this.clientName = item.name
					}
				})
			},1000)
        },
        methods: {
			// 客户名称搜索
			searchClient() {
			    this.http({
			        url: this.api.get_user_list,
			        method: "POST",
			        data: {
			            uid: this.uid,
			            search: this.textClient
			        },
			        success: res => {
			            this.clientList = res
			        }
			    })
			},
			// 选择商品分类
			changeWarehouse(e,index) {
				this.list[index].wIndex = e.target.value
				this.list[index].cate_id = this.warehouse[e.target.value].id
				// this.list[index].cangku = this.warehouse[e.target.value].warehouse_name
			},
			// 选择工艺
			changeWarehouse_gongyi(e,index) {
				this.workmanship_data[index].gIndex = e.target.value
				this.workmanship_data[index].cate_id = this.warehouse_gongyi[e.target.value].id
				// this.list[index].cangku = this.warehouse[e.target.value].warehouse_name
			},
			// 名称选择
            openModal(name, index) {
                this.preventEvent = true
                if (name == "client") {
                    this.showClient = true
					// this.kehu_num = 1
                } else if (name == "good") {
					this.itemIndex = index
					if(this.list[index].cate_id !=='') {
						this.showGood = true
						this.http({
						    url: this.api.get_goods_data,
						    method: "POST",
						    data: {
						        uid: this.uid,
								cate_id:this.list[index].cate_id
						    },
						    success: res => {
						        this.goodList = res
						    }
						})
					} else {
						uni.showToast({
							title:'请先选择商品分类',
							icon:'none',
							duration: 2000
						})
					}
					
                } else if(name =="clientlist") {
					this.itemIndex = index
					this.showClient = true
					// this.kehu_num = 2
				}else if (name == "gongyi") {
					this.itemgIndex = index
					if(this.workmanship_data[index].cate_id !=='') {
						this.gongyiGood = true
						this.http({
						    url: this.api.get_workmanship_data,
						    method: "POST",
						    data: {
						        uid: this.uid,
								cate_id:this.workmanship_data[index].cate_id
						    },
						    success: res => {
						        this.gongyiList = res
						    }
						})
					} else {
						uni.showToast({
							title:'请先选择工艺分类',
							icon:'none',
							duration: 2000
						})
					}
					
                }
            },
			// 弹窗关闭按钮
            closeModal(name) {
                this.preventEvent = false
                if (name == "client") {
                    this.showClient = false
                } else if (name == "good") {
                    this.showGood = false
                } else if (name == 'clientlist') {
					this.showClient = false
				}else if (name == 'gongyi') {
					this.gongyiGood = false
				}
            },
			// 客户名称搜索
            searchClient() {
                this.http({
                    url: this.api.goodsGet_apply,
                    method: "POST",
                    data: {
                        uid: this.uid,
                        name: this.textClient
                    },
                    success: res => {
                        this.clientList = res
                    }
                })
            },
			// // 商品搜索
   //          searchGood(index) {
   //              this.http({
   //                  url: this.api.goodsList,
   //                  method: "POST",
   //                  data: {
   //                      uid: this.uid,
			// 			name: this.textGood
   //                  },
   //                  success: res => {
   //                      this.goodList = res
   //                  }
   //              })
   //          },
			// 客户选择点击
            selectClient(i) {
				this.clientId = this.clientList[i].id
				this.clientName = this.clientList[i].name
				this.showClient = false
                this.preventEvent = false
            },
			// 商品选择点击
            selectGood(i,e) {
                this.prevent = false
                this.showGood = false
                let gid = this.goodList[i].id
                let exist = this.isExist(gid)
                if (exist) {
                    uni.showToast({
                        title: "请勿重复添加商品",
                        icon: "none"
                    })
                    return
                }
				this.list[this.itemIndex].goods_id = this.goodList[i].id
				this.list[this.itemIndex].goods_name = this.goodList[i].name
				this.list[this.itemIndex].unit_name = this.goodList[i].unit_name
            },
			// 工艺选择点击
			selectGongyi(i,e) {
			    this.prevent = false
			    this.gongyiGood = false
			    let gid = this.gongyiList[i].id
			    let exist = this.isExistGongyi(gid)
			    if (exist) {
			        uni.showToast({
			            title: "请勿重复添加工艺",
			            icon: "none"
			        })
			        return
			    }
				this.workmanship_data[this.itemgIndex].workmanship_id = this.gongyiList[i].id
				this.workmanship_data[this.itemgIndex].project_name = this.gongyiList[i].project_name
				this.workmanship_data[this.itemgIndex].unit_name = this.gongyiList[i].unit_name
			},
            close() {
                this.preventEvent = false
            },
			// 添加商品按钮
            add(eve) {
				console.log('eve',eve)
				if(eve == 'shop') {
					let item = {
					    goods_id: 0,
					    goods_name: "",
					    cate_id:"",
					    wIndex: -1,
					    goods_quantity:0,
					    total_price:0,
					    price:0,
					    unit_name:''
					}
					this.list.push(item)
				} else if(eve == 'gongyi') {
					let item = {
					    workmanship_id: 0,
					    project_name: "",
					    cate_id:"",
					    gIndex: -1,
					    quantity:0,
					    workmanship_price:0,
					    workmanship_sub_total:''
					}
					this.workmanship_data.push(item)
				}
                
            },
			// 商品删除按钮
            del(index,eve) {
				if (eve == 'shop') {
					if (this.list.length == 1) {
						uni.showToast({
							title:'最少添加一个商品',
							icon:'none',
							duration: 2000
						})
					} else{
						this.list.splice(index, 1)
						this.total_money = 0
						this.total_count = 0
						this.order_total_money = 0
						this.list.map((item,index) =>{
							this.total_money = Number(this.total_money)+Number(item.total_price)
							this.total_count = Number(this.total_count)+Number(item.goods_quantity)
						})
						if(this.discount == '') {
							this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
						} else {
							this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
						}
					}
				} else if(eve == 'gongyi') {
					if (this.workmanship_data.length == 1) {
						uni.showToast({
							title:'最少添加一个工艺',
							icon:'none',
							duration: 2000
						})
					} else{
						this.workmanship_data.splice(index, 1)
						this.workmanship_total_count = 0
						this.workmanship_total_money = 0
						this.order_total_money = 0
						this.workmanship_data.map((item,index) =>{
							this.workmanship_total_count = Number(this.workmanship_total_count)+Number(item.quantity)
							this.workmanship_total_money = Number(this.workmanship_total_money)+Number(item.workmanship_sub_total)
						})
						if(this.discount == '') {
							this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
						} else {
							this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
						}
					}
				}
				
            },
			// 商品数量  总价
            count(e, i ,j) {
				const a = e.target.value
				const inputRule = /[^\d]/g
				this.$nextTick(() => {
					if(j == 'shop') {
						this.list[i].goods_quantity = a.replace(inputRule,'')
						if(this.list[i].goods_quantity == '') {
							this.total_money = 0
							this.total_count = 0
							this.order_total_money = 0
							this.list[i].total_price = 0
							return
						} else {
							this.list[i].total_price = (this.list[i].price * this.list[i].goods_quantity).toFixed(2)
							this.total_money = 0
							this.total_count = 0
							this.order_total_money = 0
							this.list.map((item,index) =>{
								this.total_money = Number(this.total_money)+Number(item.total_price)
								this.total_count = Number(this.total_count)+Number(item.goods_quantity)
							})
							if(this.discount == '') {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*1
							} else {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*(this.discount/100)
							}
						}
						
					} else {
						this.workmanship_data[i].quantity = a.replace(inputRule,'')
						if(this.workmanship_data[i].quantity == '') {
							this.workmanship_total_count = 0
							this.workmanship_total_money = 0
							this.order_total_money = 0
							this.workmanship_data[i].workmanship_sub_total = 0
							return
						} else {
							this.workmanship_total_count = 0
							this.workmanship_total_money = 0
							this.order_total_money = 0
							
							this.workmanship_data[i].workmanship_sub_total = (this.workmanship_data[i].workmanship_price * this.workmanship_data[i].quantity).toFixed(2)
							this.workmanship_data.map((item,index) =>{
								this.workmanship_total_count = Number(this.workmanship_total_count)+Number(item.quantity)
								this.workmanship_total_money = Number(this.workmanship_total_money)+Number(item.workmanship_sub_total)
							})
							if(this.discount == '') {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*1
							} else {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*(this.discount/100)
							}
						}
						
					}
				})
				
                
            },
			// 商品单价
			price(e, i ,j) {
				const a = e.target.value
				const inputRule = /[^\d]/g
				this.$nextTick(() => {
					if(j == 'shop') {
						this.list[i].price = a.replace(inputRule,'')
						if(this.list[i].price == '') {
							this.total_money = 0
							this.total_count = 0
							this.order_total_money = 0
							this.list[i].total_price = 0
							return
						} else {
							this.list[i].total_price = (this.list[i].price * this.list[i].goods_quantity).toFixed(2)
							this.total_money = 0
							this.total_count = 0
							this.order_total_money = 0
							this.list.map((item,index) =>{
								this.total_money = Number(this.total_money)+Number(item.total_price)
								this.total_count = Number(this.total_count)+Number(item.goods_quantity)
							})
							if(this.discount == '') {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*1
							} else {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*(this.discount/100)
							}
						}
					} else {
						this.workmanship_data[i].workmanship_price = a.replace(inputRule,'')
						if(this.workmanship_data[i].workmanship_price == '') {
							this.workmanship_total_count = 0
							this.workmanship_total_money = 0
							this.order_total_money = 0
							this.workmanship_data[i].workmanship_sub_total = 0
							return
						} else {
							this.workmanship_data[i].workmanship_sub_total = (this.workmanship_data[i].workmanship_price * this.workmanship_data[i].quantity).toFixed(2)
							this.workmanship_total_count = 0
							this.workmanship_total_money = 0
							this.order_total_money = 0
							this.workmanship_data.map((item,index) =>{
								this.workmanship_total_count = Number(this.workmanship_total_count)+Number(item.quantity)
								this.workmanship_total_money = Number(this.workmanship_total_money)+Number(item.workmanship_sub_total)
							})
							if(this.discount == '') {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*1
							} else {
								this.order_total_money = (this.total_money + this.workmanship_total_money)*(this.discount/100)
							}
						}
					}
				})
				
			    
			},
			// 折扣
			discount_text(e) {
				const a = e.target.value
				const inputRule = /[^\d]/g
				this.$nextTick(() =>{
					this.discount = a.replace(inputRule,'')
					if(this.discount>100) {
						this.discount = 100
					} else if(this.discount<0) {
						this.discount = 1
					}
					if(this.discount == '') {
						this.order_total_money = (this.total_money + this.workmanship_total_money)*1
					} else {
						this.order_total_money = (this.total_money + this.workmanship_total_money)*(this.discount/100)
					}
				})
				
			},
            isExist(goodId) {
                let bool = false
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].goods_id == goodId) {
                        bool = true
                    }
                }
				for (let i = 0; i < this.workmanship_data.length; i++) {
				    if (this.workmanship_data[i].workmanship_id == goodId) {
				        bool = true
				    }
				}
                return bool
            },
			isExistGongyi(goodId) {
			    let bool = false
				for (let i = 0; i < this.workmanship_data.length; i++) {
				    if (this.workmanship_data[i].workmanship_id == goodId) {
				        bool = true
				    }
				}
			    return bool
			},
            updatePriceArr() {
                let arr = []
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].goods_id != 0) {
                        arr.push(this.list[i])
                    }
                }
                return arr
            },
			updatePriceArrGongyi() {
			    let arr = []
				for (let i = 0; i < this.workmanship_data.length; i++) {
				    if (this.workmanship_data[i].workmanship_id != 0) {
				        arr.push(this.workmanship_data[i])
				    }
				}
			    return arr
			},
			
			// 返回上一页
			previous() {
				let pages = getCurrentPages(); // 当前页面
				let beforePage = pages[pages.length - 2]; // 上一页
				uni.navigateBack({
				    success: function() {
				        beforePage.onLoad(); // 执行上一页的onLoad方法
				    }
				});
			},
			// 确定添加按钮点击
            submit() {
				uni.showModal({
					title: '提示',
					content: '确定添加当前销售单吗？',
					success: res => {
						if(res.confirm) { 
							if(this.discount == '') {
								this.discount = ''
							} else {
								this.discount = this.discount/100
							}
							this.http({
							    url: this.api.budget_submit,
							    method: "POST",
							    data: {
									uid: this.uid,
									// id:0,
									name:this.name,//客户姓名
									address:this.address,//客户地址
									mobile_one:this.mobile_one,//联系方式1
									mobile_two:this.mobile_two,//联系方式2
									discount:this.discount,//折扣
									remarks:this.remarks,//备注
									create_user_id:this.clientId,
									total_money:this.total_money,
									total_count:this.total_count,
									workmanship_total_count:this.workmanship_total_count,
									workmanship_total_money:this.workmanship_total_money,
									order_total_money:this.order_total_money,
									goods_data:JSON.stringify(this.updatePriceArr(this.list)),
									workmanship_data:JSON.stringify(this.updatePriceArrGongyi(this.workmanship_data))
							    },
							    success: res => {
									uni.showToast({
										title:'添加成功',
										icon:'none',
										duration: 2000
									})
									setTimeout(() =>{
										this.previous()
									},500) 
							    }
							})
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
            }
        }
    }
